.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.game-container {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: none;
}

.mobile-controls {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.joystick {
  position: absolute;
  width: 100px;
  height: 100px;
  pointer-events: all;
}

.control-button {
  position: absolute;
  width: 80px;
  height: 80px;
  bottom: 10px;
  pointer-events: all;
  opacity: 0.7;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 16px;
}

#fireButton {
  right: 10px;
  bottom: 100px;
}

#switchButton {
  left: 10px;
  bottom: 100px;
}